





































































.legend_box{
  ul{
    background: #fff;
    padding:5px 10px;
    li{
      padding: 5px;
      display:flex;
      align-items: center;
      i{
        display: inline-block;
      }
      span{
        margin-left: 10px
      }
    }
  }
  &#legend {
    ul {
      background: initial;
      padding: 0;
    }
  }
}
