  width: 100%;
  height: 100%;
  position: relative;
  /* 地图框样式 */
  #viewDiv {
    width: 100%;
    height: 100%;
  }
  #legendbar {
    & > span {
      background: #08304a;
      border: 1px solid #93c2e6;
      opacity: 0.8;
      color: #00e5ff;
      font-size: 16px;
      line-height: 30px;
      text-align: center;
      letter-spacing: 20px;
      padding-left: 15px;
      height: 30px;
      display: block;
    }
    .legend_box {
      background: rgba(1, 51, 102, 0.85);
      border: 1px solid rgba(147, 194, 230, 0.8);
      border-top: none;
      padding: 10px 0;
      color: #fff;
    }
  }
  #legendbar {
    position: absolute;
    right: 15px;
    bottom: 30px;
  }
  #toggleBox {
    z-index: 1;
    position: absolute;
    top: 50%;
    transform: translateY(-64%);
    left: -346px;
    transition: all ease-in 0.4s;
    h3 {
      line-height: 35px;
      background: rgba(255, 255, 255, 0.65);
      border-top-right-radius: 4px;
    }
    #searchBox {
      background: rgba(255, 255, 255, 0.6);
      border-top-right-radius: 4px;
      border-bottom-right-radius: 4px;
      .ivu-radio-group {
        overflow: hidden;
        padding: 10px 0;
        margin: 0 10px;
        border-bottom: 1px solid #333;
        display: flex;
        justify-content: center;
        .ivu-radio-group-item {
          float: left;
          padding: 5px;
          border: 1px solid initial;
          &:first-child {
            margin-left: 0;
          }
          &.ivu-radio-wrapper-checked {
            border: 1px solid #2d8cf0;
            border-radius: 4px;
          }
        }
      }
      .ivu-form {
        padding: 15px 10px 5px;
        .ivu-form-item {
          margin-bottom: 10px;
        }
      }
    }
    #toggleImg {
      position: absolute;
      right: -24px;
      top: 50%;
      transform: translateY(-50%);
      /*background: rgba(30,143,209);*/
      /*color: #fff;*/
      /*border-top-right-radius: 6px;*/
      /*border-bottom-right-radius: 6px;*/
      &:hover {
        cursor: pointer;
      }
    }
    &.activeBox {
      left: 0;
    }
    .tip {
      padding: 15px 0;
    }
  }
  #controlLayerBox {
    position: absolute;
    left: 0;
    bottom: 25px;
    width: 346px;
    h3 {
      line-height: 35px;
      background: rgba(255, 255, 255, 0.65);
    }
    #layerBox {
      background: rgba(255, 255, 255, 0.6);
      p {
        padding: 10px;
        line-height: 30px;
      }
    }
  }
  .searchBtns {
    display: flex;
    justify-content: center;
    padding-bottom: 10px;
    .ivu-btn {
      margin-left: 10px;
      &:first-child {
        margin: 0;
      }
    }
  }
  .sta_box {
    width: 30%;
    position: absolute;
    padding: 15px 20px;
    top: 80px;
    right: -5px;
    z-index: 999;
    .con_box {
      height: 100%;
      width: 100%;
      background: #fff;
      border-radius: 10px;
      box-shadow: 0 0 5px 3px rgba(0, 0, 0, 0.1);
      position: relative;
      overflow: hidden;
      .boxBtn {
        position: absolute;
        right: 10px;
        top: 10px;
        z-index: 80;
      }
      .bigBox {
        width: 100%;
        height: 100%;
        padding: 15px;
      }
      .smallToBig {
        .smallBox {
          width: 100%;
        }
      }
    }
  }
  /* box放大 */
  .activeTable {
    left: 0 !important;
    top: 0 !important;
    padding: 0 !important;
    width: 100%;
    height: 100%;
    z-index: 99;
    transition: all 0.8s ease;
  }
  .page_box {
    width: 100%;
    padding: 15px 0 0;
    text-align: right;
  }

  #switchDiv {
    position: absolute;
    top: 15px;
    left: 60px;
  }
  #switchDiv input {
    border: none;
    box-shadow: rgba(0, 0, 0, 0.3) 0px 1px 2px;
  }
}
